import  React from 'react';
import { Form, Input, Button } from 'antd';
import  styled  from 'styled-components';
import { useStores } from '../stores/index';
import { useHistory } from 'react-router-dom'

const Wraper = styled.div`
    max-width:600px;
    box-shadow: 2px 2px 4px 0 rgba(0,0,0,.2);
    border-radius:4px;
    padding:20px;
    margin:30px auto;
`
const Title = styled.h1`
    text-align:center;
    margin-bottom:30px;
`

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 18 },
};
const tailLayout = {
  wrapperCol: { offset: 6, span: 18 },
};


const validateUsername = (rule, value) => {
        if (/\W/.test(value)) return Promise.reject('只能时字母数字下划线')
        if (value.length < 4 || value.length >10) return Promise.reject('长度为4~10个字符')
        return Promise.resolve()
    }


const confirmPassword = ({ getFieldValue }) => ({
    validator(rule, value){
        if (!value || getFieldValue('password') === value) {
            return Promise.resolve()
        }
        return Promise.reject('两次密码不匹配')
    }
})

const Compenent = () => {
    const {AuthStore} = useStores()
    const history = useHistory()

    const onFinish = values => {
      console.log('Success:', values);
      AuthStore.setUsername(values.username)
      AuthStore.setPassword(values.password)
      AuthStore.register()
          .then(() => {
              console.log('注册成功');
              history.push('/')
          }).catch(() => {
              console.log('注册失败，什么也不做');
          })
  };

//   const onFinishFailed = (errorInfo) => {
//     console.log('Failed:', errorInfo);
//   };

    return (
        <Wraper>
            <Title>注册</Title>
            <Form
            {...layout}
                name="basic"
                onFinish={onFinish}
            >
                <Form.Item
                    label="用户名"
                    name="username"
                    rules={[{ required: true, message: '输入用户名' },{validator:validateUsername}]}
                >
                    <Input />
                </Form.Item>

                <Form.Item
                    label="密码"
                    name="password"
                    rules={[
                        { required: true, message: '输入密码' },
                        {
                            min: 4,
                            message:'最少4个字符'
                        },
                        {
                            max: 10,
                            message:'最大10个字符'
                        }

                    ]}
                >
                    <Input.Password />
                </Form.Item>
                    
                <Form.Item
                    label="确认密码"
                    name="confirmPasswprd"
                    rules={[{ required: true, message: '再次确认密码' },confirmPassword]}
                >
                    <Input.Password />
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" >
                    提交
                    </Button>
                </Form.Item>
            </Form>
        </Wraper>
  );
};

export default Compenent